@use 'global' as *;

.form-textarea textarea {
  padding: rem(8) rem(15);
  width: 100%;
  height: rem(200);
  font-size: rem(15);
  border-radius: 0;
  border: 1px solid $accent-color;
  appearance: none;
  background-color: transparent;
  background-image: none;
  border-radius: 15px;
}

.form-textarea textarea:focus {
  border: 1px solid $accent-color;
  box-shadow: none;
  outline: none;
}