@use 'global' as *;

.colorbox {
  position: relative;
}

.colorbox .color{
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: $accent-color;
  border-radius: 45px;
}

.colorbox--blog .color{
  border-radius: 20px;
}

.colorbox img{
  display: block;
  width: 100%;
}