.btn-contactform {
  background: transparent;
  padding: 0;
  border: 1px solid #B7AA00;
  box-sizing: border-box;
  border-radius: 75px;
  position: relative;
  z-index: 1;
  display: inline-flex;
  transition: 0.5s;
  color: #fff;
  max-width: 12.5625rem;
  width: 100%;
  cursor: pointer;
}

.btn-contactform__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn-contactform::before,
.btn-contactform::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 75px;
}

.btn-contactform::before {
  background: #B7AA00;
  z-index: -1;
  transition: transform ease 0.5s, opacity 0.5s;
}

.btn-contactform::after {
  border: 1px solid #B7AA00;
  box-sizing: border-box;
  z-index: -1;
}

.btn-contactform input {
  padding: 1.125rem 0;
  z-index: 1;
}

.btn-contactform input[type=submit] {
  width: 100%;
  color: transparent;
}

.btn-contactform:hover::before {
  transform: scale(0);
  opacity: 0;
}

.btn-contactform:hover span {
  color: #B7AA00;
}

.btn-h {
  width: 100%;
  height: 3.75rem;
  display: table;
  position: fixed;
  transition: 0.3s;
  margin: 0 auto;
  z-index: -1;
  opacity: 0;
  bottom: 0;
}

.btn-h.fixed {
  opacity: 1;
  z-index: 100;
}

.btn-h a {
  color: #fff;
