.toggle-list__item-question,
.toggle-list__item-answer {
  color: #B7AA00;
  font-size: 1.25rem;
  line-height: 1.75;
  padding-left: 2rem;
  cursor: pointer;
}

.toggle-list__item-question {
  position: relative;
  text-indent: -0.1875rem;
  cursor: pointer;
}
.toggle-list__item-question ul {
  display: none;
}

.toggle-list__item-top {
  padding: 0.1875rem 0;
  text-indent: 0.0625rem;
}

.toggle-list__item-answer {
  position: relative;
  margin-left: 1.5rem;
}
.toggle-list__item-question::before,
.toggle-list__item-answer::before {
  content: "";
  position: absolute;
  top: 25%;
  left: 0.1875rem;
  height: calc(0.65625rem * tan(60deg));
  width: 1.3125rem;
  clip-path: polygon(50% 0, 100% 100%, 0 100%);
  background-color: #B7AA00;
  transition: 0.3s;
}
.toggle-list__item-question::before {
  transform: rotate(180deg);
}

.toggle-list__item-question--two::before {
  transform: rotate(90deg);
}

.toggle-list__item-answer::before {
  transform: rotate(90deg);
}

.toggle-list__item-question.is-open::before {
  transform: rotate(90deg);
}

.toggle-list__item-question--two.is-open::before {
  transform: rotate(180deg);
}

.toggle-list__item-answer.is-open::before {
  transform: rotate(180deg);
}

